<template>
  <div class="day">
      <h1 class="day-title">
        {{$route.params.date}}
      </h1>

      <ul class="day-list">
        <li v-for="item in items" :key="item" class="day-list-item">
          <div class="time">
            <h2>
              {{item.id}}
            </h2>
            <div class="action">
              <input type="text" v-model="item.action" placeholder="한 일을 작성해주세요">
            </div>
            <day-score
            @onUpdateScore="onUpdateScore"
            :item="item"
            >
            </day-score>
            <div class="note">
              <textarea v-model="item.note" placeholder="노트를 작성해주세요"></textarea>
            </div>
          </div>
        </li>
      </ul>
  </div>
</template>

<script>
import DayScore from './DayScore'
export default {
  name: 'Day',
  methods : {
    onUpdateScore(item, score) {
      console.log('Day comp', score);
      item.score = score;

    }
  },
  computed : {},
  components:{
    DayScore
  },
  data() {
      return {
        items:[
          {
            action:"",
            id:"2018-04-01-08-10",
            note:"",
            open:false,
            score:undefined,
          },
          {
            action:"",
            id:"2018-04-01-10-12",
            note:"",
            open:false,
            score:undefined,
          }
        ]
      }
  },
  props:{}
}
</script>

